}

.application-form__wrapper--data {
  margin-top: 42px;
}

.application-form__input--phone {
  margin-top: 26px;
}

.application-form__field--phone::after {
  display: block;
  background-image: url("../img/images-svg/phone.svg");
  background-repeat: no-repeat;
  background-position: center right;
  background-size: cover;
  width: 22px;
  height: 21px;
  content: "";
}

.application-form__field--email::after {
  display: block;
  background-image: url("../img/images-svg/mail.svg");
  background-repeat: no-repeat;
  background-position: center right;
  background-size: cover;
  width: 22px;
  height: 21px;
  content: "";
}

.application-form__radiobutton {
  margin-top: 11px;
  display: flex;
}
.application-form__radiobutton:last-child {
  margin-bottom: 0;
}
.application-form__radiobutton .application-form__label-radiobutton {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  margin-right: 10px;
  align-self: center;
}
.application-form__radiobutton .application-form__input-radiobutton {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
.application-form__radiobutton .application-form__input-radiobutton:checked ~ .application-form__checkmark::after {
  display: block;
}
.application-form__radiobutton .application-form__input-radiobutton:disabled ~ .application-form__checkmark::after {
  opacity: 0.3;
}
.application-form__radiobutton .application-form__checkmark {
  position: absolute;
  top: -5px;
  left: 0;
  height: 30px;
  width: 30px;
  border: 1px solid #444444;
  border-radius: 50%;
}
.application-form__radiobutton .application-form__checkmark::after {
  content: "";
  position: absolute;
  display: none;
  top: 8px;
  left: 8px;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background-color: #63D1BB;
}

.application-form__checkbox {
  margin-top: 23px;
}
.application-form__checkbox:hover {
  color: #222222;
}
.application-form__checkbox:active {
  color: #222222;
  opacity: 0.3;
}
.application-form__checkbox .application-form__label-checkbox {
  display: block;
  position: relative;
  padding-left: 35px;
  cursor: pointer;
  font-size: 22px;
  user-select: none;
}
.application-form__checkbox .application-form__input-checkbox {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
.application-form__checkbox .application-form__checkmark {
  position: absolute;
  left: 0;
  height: 38px;
  width: 38px;
  border: 1px solid #444444;
}
.application-form__checkbox .application-form__checkmark::after {
  content: "";
  position: absolute;
  display: none;
  top: 10px;
  left: 5px;
  width: 33%;
  height: 57%;
  border: solid #000000;
  border-width: 0 1px 1px 0;
  transform: rotate(45deg);
}
.application-form__checkbox .application-form__checkbox .application-form__checkmark {
  width: 30px;
  height: 30px;
}
.application-form__checkbox .application-form__checkbox .application-form__checkmark {
  position: absolute;
  left: 0;
  top: -5px;
  height: 30px;
  width: 30px;
  border: 1px solid #cdcdcd;
}
.application-form__checkbox .application-form__checkmark::after {
  top: 1px;
  left: 8px;
}
.application-form__checkbox .application-form__input-checkbox:checked ~ .application-form__checkmark::after {
  display: block;
}
.application-form__checkbox .application-form__input-checkbox:disabled ~ .application-form__checkmark::after {
  display: none;
}

